export function isObjNull(obj) {
    return (obj == null || obj == undefined)
}

export function isObjEmpty(obj) {
    return isObjNull(obj) || obj.length == 0
}

export function isEmptyObject(obj) {
    if (isObjNull(obj)) {
        return false
    }
    if (obj.length == 0) {
        return false
    }
    for (var n in obj) {
        return false
    }
    return true;
}

export function strContain(parent, child) {
    if (isObjEmpty(parent)) {
        return false
    }
    return (parent.indexOf(child) != -1)
}

export function getStrValue(object, key) {
    if (isObjNull(object)) {
        return ''
    }
    if (isObjNull(object[key])) {
        return ''
    }
    return object[key]
}

export function getIntValue(object, key) {
    if (isObjNull(object)) {
        return 0
    }
    if (isObjNull(object[key])) {
        return 0
    }
    return object[key]
}

export function getTimeValue(object, key) {
    if (isObjNull(object)) {
        return 0
    }
    if (isObjNull(object[key])) {
        return 0
    }
    let result = object[key];
    if (typeof result == 'string') {
        try {
            let time = Date.parse(new Date(result))
            return time
        } catch (e) {
            return result
        }
    } else {
        return result
    }
}

export function getArrayValue(object, key) {
    if (isObjNull(object)) {
        return []
    }
    if (isObjNull(object[key])) {
        return []
    }
    return object[key]
}

export function getObjValue(object, key) {
    if (isObjNull(object)) {
        return {}
    }
    if (isObjNull(object[key])) {
        return {}
    }
    return object[key]
}

export function getBracketStr(text) {
    let result = ''
    if (isObjEmpty(text))
        return result
    let regex = /\[(.+?)\]/g;
    let options = text.match(regex)
    if (!isObjEmpty(options)) {
        let option = options[0]
        if (!isObjEmpty(option)) {
            result = option.substring(1, option.length - 1)
        }
    }
    return result
}

export function getParenthesesStr(text) {
    let result = ''
    if (isObjEmpty(text))
        return result
    let regex = /\((.+?)\)/g;
    let options = text.match(regex)
    if (!isObjEmpty(options)) {
        let option = options[0]
        if (!isObjEmpty(option)) {
            result = option.substring(1, option.length - 1)
        }
    }
    return result
}

String.prototype.replaceAll = function (reg, s) {
    if (isObjEmpty(this)) {
        return ''
    }
    return this.replace(new RegExp(reg, 'gm'), s)
}


/**
 * Mapjson
 * @param m
 * @returns String
 */
export function MapToJson(m) {
    if (isObjEmpty(m)) {
        return ''
    }
    var str = '{';
    var i = 1;
    m.forEach(function (item, key, mapObj) {
        if (mapObj.size == i) {
            str += '"' + key + '":"' + item + '"';
        } else {
            str += '"' + key + '":"' + item + '",';
        }
        i++;
    });
    str += '}';
    return str;
}